.imageAndText img {
    width: 48px;
    height: 48px;
    position: absolute;
    left: 536px;
    top: 10px
}

.text {
    display: block;
    width: 527px;
    background: #0066cc;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03),
    0px 1px 2px rgba(29, 33, 38, 0.1);
    padding: 7px 25px 7px 18px;
    border-radius: 10px 0px 0px 10px;
    clip-path: polygon(
            0% 0%,
            98% 1%,
            98% 87%,
            100% 100%,
            75% 100%,
            49% 100%,
            0 100%
    );
}

