.textarea {
    padding: 10px;
    margin-top: 50px;
    margin-right: 40px;
    margin-left: 80px;
    min-width: 976px;
    min-height: 40px;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
}

.button {
    position: relative;
    width: 120px;
    height: 40px;
    top: -20px;
    background: #0066CC;
    border-radius: 20px;
    border: none;
    color: #ffffff;
}

.button:hover {
    background: #115396;
}
.button:active {
    top: -17px;
}
