.friendMessage {
    position: relative;
}

.friendImageAndText {

}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    position: absolute;
    top: 10px;
}

.friendText {
    margin-left: 60px;
    display: block;
    width: 527px;
    height: 60px;
    background: #0066cc;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03),
    0px 1px 2px rgba(29, 33, 38, 0.1);
    padding: 7px 7px 13px 25px;
    clip-path: polygon(2% 0, 100% 0%, 100% 75%, 100% 100%, 75% 100%, 0 100%, 2% 86%);
    border-radius: 0px 10px 10px 0px;
}

.friendText:after {

}

.friendName {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    text-align: left;
    color: #ffffff;
}

.friendMessageText {
    text-align: left;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
}

.friendTime {
    text-align: left;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    position: relative;
    top:5px;
    left:12px;
}
