.sidebar {
    /*position: fixed;*/
    left: -300px;
    top: 0;
    bottom: 0;
    width: 300px;
    background-color: #fff;
    z-index: 9999;
    transition: left 0.3s ease-in-out;
}

.sidebar.open {
    left: 0;
}

.background {
    /*position: fixed;*/
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
}

.nav {
    margin-top: 50px;
}

.nav a {
    display: block;
    padding: 10px 20px;
    color: #333;
    text-decoration: none;
}

.nav a:hover {
    background-color: #f5f5f5;
}

.nav a.active {
}
.close {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
}

.close img {
    width: 20px;
    height: 20px;
}

.active{
    background-color: #f5f5f5;
    font-weight: bold;
}
